import React,{ useState, useEffect, useRef, useCallback } from 'react'
import styled from 'styled-components'
import { Flex, Box, SwapVertIcon, IconButton } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import { Pool } from 'state/types'
import useIntersectionObserver from 'hooks/useIntersectionObserver'
import useGetTopFarmsByApr from 'views/Home/hooks/useGetTopFarmsByApr'
import useGetTopPoolsByApr from 'views/Home/hooks/useGetTopPoolsByApr'
import TopFarmPool from './TopFarmPool'
import RowHeading from './RowHeading'

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(2, auto);

  ${({ theme }) => theme.mediaQueries.sm} {
    display: grid;
    grid-template-columns: repeat(2, auto);
  }

  ${({ theme }) => theme.mediaQueries.md} {
    display: grid;
    grid-template-columns: repeat(2, auto);
  }
` 

const FarmsPoolsRow = () => {
  const [showFarms, setShowFarms] = useState(false)
  const { t } = useTranslation()
  const { observerRef, isIntersecting } = useIntersectionObserver()
  const { topFarms } = useGetTopFarmsByApr(isIntersecting)
  const { topPools } = useGetTopPoolsByApr(isIntersecting)

  const timer = useRef<ReturnType<typeof setTimeout>>(null)
  const isLoaded = topFarms[0] && topPools[0]

  const startTimer = useCallback(() => {
    timer.current = setInterval(() => {
      setShowFarms((prev) => !prev)
    }, 6000)
  }, [timer])

  useEffect(() => {
    if (isLoaded) {
      startTimer()
    }

    return () => {
      clearInterval(timer.current)
    }
  }, [timer, isLoaded, startTimer])

  const getPoolText = (pool: Pool) => {
    if (pool.isAutoVault) {
      return t('Auto CAKE')
    }

    if (pool.sousId === 0) {
      return t('Manual CAKE')
    }

    return t('Stake %stakingSymbol% - Earn %earningSymbol%', {
      earningSymbol: pool.earningToken.symbol,
      stakingSymbol: pool.stakingToken.symbol,
    })
  }

  const getPoolText1 = (pool: Pool) => {
    if (pool.isAutoVault) {
      return t('Auto CAKE')
    }

    if (pool.sousId === 0) {
      return t('Manual CAKE')
    }

    return t('Stake %stakingSymbol%', {
      stakingSymbol: pool.stakingToken.symbol,
    })
  }

  const getPoolText2 = (pool: Pool) => {
    if (pool.isAutoVault) {
      return t('Auto CAKE')
    }

    if (pool.sousId === 0) {
      return t('Manual CAKE')
    }

    return t('Earn %earningSymbol%', {
      earningSymbol: pool.earningToken.symbol,
    })
  }

  
  const getPoolText3 = (pool: Pool) => {
    if (pool.isAutoVault) {
      return t('Auto CAKE')
    }

    if (pool.sousId === 0) {
      return t('Manual CAKE')
    }

    return t('', {
    })
  }


  return (
    <div ref={observerRef}>
      <Flex flexDirection="column" mb="170px" mt="-40px" >
        <Flex mb="34px" justifyContent="center" alignItems="center">
          <RowHeading text={showFarms ? t('Top Minings') : t('Top Staking Pools')} />
          <IconButton
            variant="text"
            height="100%"
            width="auto"
            onClick={() => {
              setShowFarms((prev) => !prev)
              clearInterval(timer.current)
              startTimer()
            }}
          >
            <SwapVertIcon height="24px" width="24px" color="textSubtle" />
          </IconButton>
        </Flex>
        <Flex alignItems="center" justifyContent="center">
        <Box height={['200px', null, '200px']} width={['100%', null, '450px']}>
        <Flex flexDirection="column">
                  {topFarms.map((topFarm, index, topPool) => (
              <TopFarmPool
                // eslint-disable-next-line react/no-array-index-key
                key={index}
                image={topFarm?.image}
                title={topFarm?.lpSymbol} 
                title1={topFarm?.lpSymbol3}
                percentage={topFarm?.apr + topFarm?.lpRewardsApr}
                index={index}
                visible={showFarms}
              />
            ))}
            {topPools.map((topPool, index) => (
              <TopFarmPool
                // eslint-disable-next-line react/no-array-index-key
                key={index}
                image={topPool?.image}
                title={topPool && getPoolText1(topPool) } 
                title1={topPool && getPoolText2(topPool) } 
                percentage={topPool?.apr}
                index={index}
                visible={!showFarms}
              />
            ))}
          </Flex>
        </Box>
        </Flex> 
      </Flex>
    </div>
  )
}

export default FarmsPoolsRow
